const sliders = (slides, dir, prev, next) => {
    let slideIndex = 1, //начальный слайд
        paused = false; //переменная для остановки автоматической прокрутки слайдера

    const items = document.querySelectorAll(slides);
          

    function showSlides(n) { 
        //задаем ограничения показа слайдов
        if (n > items.length) { 
            slideIndex = 1;
        }

        if (n < 1) {
            slideIndex = items.length;
        }
        ///////////////////////

        items.forEach(item => { //добавляем слайдам анимацию и изначально скрываем все слайды
            item.classList.add('animated');
            item.style.display = 'none';
        });

        items[slideIndex - 1].style.display = 'block'; //показываем первый слайд
    }

    showSlides(slideIndex);

    function plusSlides(n) {
        showSlides(slideIndex += n);
    }

    try {
        const prevBtn = document.querySelector(prev),
              nextBtn = document.querySelector(next);

        prevBtn.addEventListener('click', () => { //перелистывание слайдов вперд и назад
            plusSlides(-1);
            items[slideIndex - 1].classList.remove('slideInLeft');
            items[slideIndex - 1].classList.add('slideInRight');

        });

        nextBtn.addEventListener('click', () => { //перелистывание слайдов вперд и назад
            plusSlides(1);
            items[slideIndex - 1].classList.remove('slideInRight');
            items[slideIndex - 1].classList.add('slideInLeft');

        });

    } catch(e) {}

    //отключение авто прокрутки слайдеров
    function activateAnimation() {
        //Направление слайдеров
        if (dir === 'vertical') {
            paused = setInterval(function() {
                plusSlides(1);
                items[slideIndex - 1].classList.add('slideInDown');
            }, 3000)
        } else {
            paused = setInterval(function() {
                plusSlides(1);
                items[slideIndex - 1].classList.remove('slideInRight');
                items[slideIndex - 1].classList.add('slideInLeft');
            }, 3000)
        }
    }
    activateAnimation()
    //Отключение автопрокрутки при наведении мыши на слайдер
    items[0].parentNode.addEventListener('mouseenter', () => {
        clearInterval(paused);
    });
    //Включаем автопрокрутку когда уводим курсор со слайдера
    items[0].parentNode.addEventListener('mouseleave', () => {
        activateAnimation();
    });
    
    

};

export default sliders;